@keyframes cortex-reload-rotate {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}

.cortex-text-editor {

	&.invalid {
		@include relative;
		border: 5px solid #dc3232;
	}

	.cortex-reload {

		@include absolute(top 12px left 12px);
		background: #dc3232 url(../images/reload.svg) no-repeat center center;
		background-size: 18px 18px;
		border-radius: 22px;
		cursor: pointer;
		height: 32px;
		width: 32px;
		z-index: 1000;

		&.cortex-reload-reloading {
			animation: cortex-reload-rotate infinite 1s linear;
		}

		&:hover {
			background-color: #f73a3a;
		}
	}
}

.cortex-create-block-page,
.cortex-update-block-page {

	.wrap {
		margin: 12px;
		margin-left: 54px;
		margin-right: 54px;
	}

	.media-modal {
		bottom: 100px;
	}

	#wpcontent {
		margin: 0px !important;
		padding: 0px;
		padding-bottom: 100px;
	}

	#wpadminbar {
		display: none !important;
	}

	#adminmenumain {
		display: none !important;
	}

	#footer-left,
	#footer-upgrade {
		display: none !important;
	}

	#screen-meta-links {
		display: none !important;
	}

	.wp-header-end {
		display: none !important;
	}

	.wp-heading-inline {
		display: none !important;
	}

	.update-nag {
		display: none;
	}

	.page-title-action {
		display: none !important;
	}

	#postbox-container-1 {
		@include absolute(left 0 right 0 bottom 0);
	}

	#poststuff {
		margin: 0px !important;
		padding: 0px !important;
	}

	#post-body.columns-1,
	#post-body.columns-2,
	#post-body.columns-3 {
		margin: 0px !important;
	}

	// #post-body-content {
	// 	display: none !important;
	// }

	#submitdiv {
		@include fixed(left 0 right 0 bottom 0);
		border: none;
		border-top: 1px solid #e5e5e5;
		box-shadow: none;
		margin: 0px;
		z-index: 1000000;
	}

	#submitdiv h2,
	#submitdiv h3,
	#submitdiv .handlediv,
	#submitdiv #minor-publishing {
		display: none;
	}

	#submitdiv #major-publishing-actions {
		border: none;
		background: #fff;
	}

	#delete-action {
		display: none;
	}

	#acf-field-group-locations,
	#acf-field-group-options,
	#commentstatusdiv,
	#commentsdiv {
		display: none !important;
	}
}

.cortex-update-block-page {

	#delete-action {
		display: none;
	}

	.page-title-action {
		display: none;
	}
}

/* Preview */

.cortex-preview {

	@include relative;
	min-height: 100px;
	max-height: 500px;

	.cortex-preview-image, canvas {
		background-position: center center;
		background-repeat: no-repeat;
		background-size: contain;
		display: block;
		max-width: none;
		max-height: none;
		height: auto;
		width: 100%;
	}
}

.cortex-preview-spinner {
	@include absolute(top 50% left 50%);
	background: url(../images/spinner.gif) no-repeat;
	background-size: 20px 20px;
	content: "";
	display: block;
	margin-top: -10px;
	margin-left: -10px;
	height: 20px;
	width: 20px;
}

/* Gutenberg */

.edit-post-layout__content {
	background: #F5F5F5;
}

.edit-post-layout__metaboxes {
	background: #FFFFFF;
}

.edit-post-layout__metaboxes:not(:empty) .edit-post-meta-boxes-area {
	margin: 0px;
}

.edit-post-layout__metaboxes .acf-postbox > .inside {
	padding: 8px 6px !important;
}

.mce-tinymce {
	box-shadow: none !important;
}

.mce-toolbar-grp {
	background: #FFFFFF !important;
}

.block-editor-block-list__layout .block-editor-block-list__block.is-focus-mode:not(.is-multi-selected) {
	opacity: 1;
}

.block-editor-block-list__block.is-focus-mode:not(.is-multi-selected) > .block-editor-block-list__block-edit > .block-editor-block-contextual-toolbar {
	margin-left: 0px;
}

.wp-block {

	max-width: 960px;
	padding-top: 24px;
	padding-bottom: 24px;

	.wp-block & {
		padding-left: 72px;
		padding-right: 72px;
	}
}

.block-list-appender {
	padding-left: 46px;
	padding-right: 46px;
}

.editor-inner-blocks {

	@include relative;

	&:before {
		@include absolute(top 54px left 0 bottom 0px);
		background: #CCCCCC;
		content: "";
		display: block;
		width: 1px;
	}
}
.acf-block-title {
	font-weight: bold;
	font-size: 13px;
	padding: 16px 20px;
	border-bottom: 1px solid #EEEEEE;
	user-select: none;
}
